<script>
export default {
   name: '',
   data() {
       return {
            arr:[
                { 
                    level:'1' , 
                    levebtn:require('../../static/images/background/v11.png') , 
                    Tab:require('../../static/images/background/v12.png')},
                { 
                    level:'2' , 
                    levebtn:require('../../static/images/background/v21.png') ,
                    Tab:require('../../static/images/background/v22.png')
                },
                { 
                    level:'3' , 
                    levebtn:require('../../static/images/background/v31.png') ,
                    Tab:require('../../static/images/background/v32.png')
                },
                { 
                    level:'4' , 
                    levebtn:require('../../static/images/background/v41.png') ,
                    Tab:require('../../static/images/background/v42.png')
                }
            ],
            level1:[],
            level2:[],
            level3:[],
            level4:[],
            showIndex:0
       }
   },
   created(){
        this.$store.dispatch('pig_num');
        this.pig_num();
   },
   mounted(){
    //    this.show_pig();
   },
   methods:{
            retreat(){
                this.$router.go(-1)
            },
            Level(index,e){
                this.showIndex = index;
            },
            pig_num(){
                let data = JSON.parse(this.$store.state.pig_num);
                data.forEach( item => {
                    if( item.goodsid === 1 ){
                        this.level1.push(item);
                    }else if( item.goodsid === 2 ){
                        this.level2.push(item);
                    }else if( item.goodsid === 3 ){
                        this.level3.push(item);
                    }else if( item.goodsid === 4 ){
                        this.level4.push(item);
                    }
                } )
                // console.log( this.level1 )
                // console.log( this.level2 )
                // console.log( this.level3 )
                // console.log( this.level4 )
            },
            getRandomNum(num, times){
                let res = [];
                if (times === 1) {
                    res.push(num);
                    return res;
                };
                let max = num / times * 2;
                let current = 1 + (~~(Math.random() * max - 1));
                res.push(current);
                return res.concat(this.getRandomNum(num - current, --times));
            },
            // show_pig(){
            //     let num = this.getRandomNum(this.level1.length, 3);
